class RockPaperScissors {
  constructor() {
    //SCORES
    this.aiScore = 0;
    this.userScore = 0;
    this.choices = ["rock", "paper", "scissors"];

    //ELEMENT NODES
    //Scoreboard
    this.userScoreNode = document.querySelector(".point-user");
    this.aiScoreNode = document.querySelector(".point-ai");
    this.messageNode = document.querySelector(".Result-message");
  }

  userShoots(userPick) {
    let aiPick = this.aiShoots();
    this.determineWinner(userPick, aiPick);
  }
  aiShoots() {
    return this.choices[Math.round(Math.random() * 2)];
  }
  determineWinner(userPick, aiPick) {
    let a = this.choices.indexOf(userPick);
    let b = this.choices.indexOf(aiPick);
    if (a === b) {
      this.draw(userPick, aiPick);
    } else if ((a - b + 3) % 3 === 1) {
      this.win(userPick, aiPick);
    } else {
      this.lose(userPick, aiPick);
    }
  }
  updateScore(winner) {
    if (winner === "user") {
      this.userScore++;
      this.userScoreNode.textContent = this.userScore;
    } else {
      this.aiScore++;
      this.aiScoreNode.textContent = this.aiScore;
    }
  }
  draw(userPick, aiPick) {
    this.messageNode.textContent = `Ai picked ${aiPick}, you picked ${userPick}. It's a draw!`;
  }
  win(userPick, aiPick) {
    this.messageNode.textContent = `Ai picked ${aiPick}, you picked ${userPick}. You win!`;
    this.updateScore("user");
  }
  lose(userPick, aiPick) {
    this.messageNode.textContent = `Ai picked ${aiPick}, you picked ${userPick}. You lose! =(`;
    this.updateScore("ai");
  }
}

const Game = new RockPaperScissors();

//ELEMENT NODES
//Selection
this.rockNode = document.querySelector(".select-rock");
this.paperNode = document.querySelector(".select-paper");
this.scissorsNode = document.querySelector(".select-scissors");
//adding event listeners
this.rockNode.addEventListener("click", function (e) {
  Game.userShoots(this.dataset.selection);
});
this.paperNode.addEventListener("click", function (e) {
  Game.userShoots(this.dataset.selection);
});
this.scissorsNode.addEventListener("click", function (e) {
  Game.userShoots(this.dataset.selection);
});
